.App {
  width: 95vw;
  margin: 0 auto;
  background-color: #FD543D;
  position: relative;
  overflow: hidden;
}
.flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.search, .home ,nav{
  width: 100%;
  z-index: 1;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 5;
}
.nav > h1 {
  font-size: 2rem;
  text-decoration: overline underline;
  z-index: 1;
}
.nav ul {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav ul .link {
  text-decoration: none;
  font-size: 1.2rem;
  padding: 0.5rem;
  color: #000;
}
.link:hover {
  background-color: #fff;
  border-radius: 5px;
}
.cocktailList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  z-index: 1;
}
form {
  width: 60%;
  padding: 1rem;
  margin: 0 1rem;
}
label {
  width: 100%;
  letter-spacing: 0.1rem;
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 0.5rem;
}
input {
  width: 100%;
  border: none;
  padding: 8px;
  border-radius: 5px;
}
.cocktail {
  width: 80%;
  text-align: center;
  margin: 1rem;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 0.5rem;
  font-size: 1rem;
}
.cocktail-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: 2px solid #ccc ;
}
.cocktail-body {
  padding: 0 2rem;
  border-bottom: 2px solid #ccc ;
}
span {
  font-size: 1.2rem;
  font-style: oblique;
}
.cocktail-img-div {
  width: 100%;
}
.cocktail-img-div img {
  width: 100%;
  border-radius: 5px;
}
.divOver {
  position: absolute;
  width: 500px;
  height: 1000px;
  top: -550px;
  opacity: 0.7;
}
.divOver2 {
  background-color: yellow;
  right: -250px;
  transform: rotate(-45deg);
}
.divOver1 {
  background-color: #1FC506;
  left: -250px;
  transform: rotate(45deg);
}
